<template>
  <div class="home">
    <!-- 导航条 -->
    <van-nav-bar title="智慧商城" fixed/>
    <!-- 搜索框 -->
    <van-search @click="$router.push('/search')" shape="round" readonly :placeholder="searchTitle" />
    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in carouselList" :key="item.imgUrl">
        <img :src="item.imgUrl">
      </van-swipe-item>
    </van-swipe>
    <!-- 导航 -->
    <van-grid :column-num="5">
      <van-grid-item v-for="item in navList" :key="item.imgUrl" :icon="item.imgUrl" :text="item.text" @click="$router.push('/category')"/>
    </van-grid>
    <!-- 主会场 -->
    <div class="main">
      <img :src="mainUrl">
    </div>
    <!-- 猜你喜欢 -->
    <div class="guess">
      <div class="guess-title" v-html="likeTitle"></div>
      <div class="goods-list">
        <goods-item-vue v-for="item in proList" :key="item.goods_id" :item="item"></goods-item-vue>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsItemVue from '@/components/GoodsItem.vue'
import { getHomeData } from '@/api/home'
import { Toast } from 'vant'
export default {
  name: 'homeIndex',
  components: {
    GoodsItemVue
  },
  data () {
    return {
      searchTitle: '', // 搜索款提示
      carouselList: [], // 轮播图数据
      navList: [], // 中部导航数据
      mainUrl: '', // 主会场图片地址
      likeTitle: '', // 猜你喜欢
      proList: [] // 商品列表
    }
  },
  async created () {
    // 获取首页数据
    const { data: { pageData: { items } } } = await getHomeData()
    this.searchTitle = items[0].params.placeholder // 获取搜索款提示
    this.carouselList = items[1].data // 获取轮播图数据
    this.navList = items[3].data // 获取中部导航数据
    this.mainUrl = items[4].data[0].imgUrl
    // 一进页面提示
    Toast({
      duration: 2000,
      message: items[2].params.text
    })
    this.likeTitle = items[5].params.content // 获取猜你喜欢
    this.proList = items[6].data // 获取商品数据
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.home {
  padding: 100px 0 50px;
  // 导航条
  ::v-deep .van-nav-bar {
    z-index: 1;
    background-color: #c21401;
    .van-nav-bar__title {
      color: #fff;
    }
  }
  // 搜索框
  ::v-deep .van-search {
    position: fixed;
    width: 100%;
    background: #f1f1f2;
    top: 46px;
    z-index: 1;
  }
  // 轮播图
  .my-swipe .van-swipe-item {
    height: 185px;
    color: #fff;
    font-size: 20px;
    text-align: center;
    background-color: #39a9ed;
    img {
      width: 100%;
      height: 185px;
    }
  }
  // 主会场
  .main img {
    display: block;
    width: 100%;
  }
  // 猜你喜欢
  .guess .guess-title {
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
  // 商品样式
  .goods-list {
    background-color: #f6f6f6;
  }
}
</style>
